<template>
    <div class="wrapper">
        <div class="title">新闻列表</div>
        <div class="change" @click="change"></div>
        <el-row class="box">
            <!--选择侧栏显示块-->
            <el-row class="icon001">
                <el-button :icon=hidden.icon circle @click="change"></el-button>
            </el-row>

            <!--侧栏-->
            <el-col :span="hidden.left">
                <div class="grid-content bg-purple">
                    <el-tree class="list001" draggable accordion :data="classdata" :props="defaultProps" @node-click="handleNodeClick"></el-tree>
                </div>
            </el-col>

            <!--内容区域-->
            <el-col :span="hidden.right">
                <div class="grid-content bg-purple-light">
                    <el-collapse class="list" v-model="activeNames" @change="handleChange" accordion>
                        <el-collapse-item class="list001" v-for="(item,index) in newslist" :title="index+1+', '+item.title" :name="(index+1)" style="font-size: 30px">
                            <div class="content" v-html="item.content">{{item.content}}</div>
                        </el-collapse-item>
                    </el-collapse>
                </div>
            </el-col>

            <!--分页-->
            <div class="pagination">
                <div class="page">
                    <el-pagination background @current-change="handleCurrentChange" layout="prev, pager, next" :current-page="cur_page" :total="total" :page-size="aa">
                    </el-pagination>
                </div>
            </div>

            <!--广告-->
            <div class="ad001" :style=adview>
                <div class="ad">
                    <span class="text">小广告</span>
                    <el-row  class="icon0001">
                        <el-button class="icon0002" icon="el-icon-close" circle @click="escad"></el-button>
                    </el-row>
                </div>
                <ad></ad>
            </div>
        </el-row>


    </div>
</template>

<script>
    import Ad from "./ad";
    export default {
        name: "newlist",
        components: {Ad},
        data() {
            return {
                adview:"",
                qwerList:[],
                classdata: [],
                defaultProps: {
                    children: 'children',
                    label: 'label'
                },
                listId:[],
                arrId:0,
                parentId001:0,
                newslist:[{
                    title:'123',
                    content:'哈哈哈'
                }],
                activeNames: ['1'],
                cur_page:1,
                total:1,
                aa:10,
                hidden:{
                    key:false,
                    left:0,
                    right:24,
                    icon:"el-icon-arrow-right"
                }
            };
        },
        methods: {
            // 分页点击页数获取新闻
            handleCurrentChange(val) {
                const _this = this;
                this.cur_page = val;
                // this.getData();
                this.$ajax.post('/api/newslist',{arrId:this.parentId001,page:val}).then(function (result) {
                    if (result.data.flag){
                        // alert(result.data.msg);
                        _this.newslist = result.data.data;
                        _this.total = result.data.data[0].total;
                    } else{
                        alert(result.data.msg);
                    }
                })
            },

            handleChange(val) {
                console.log(val);
            },

            // 点击侧栏获取新闻
            handleNodeClick(data) {
                this.parentId001 = data.id;
                this.arrId = 0;
                const _this = this;
                this.$ajax.post('/api/newslist',{arrId:data.id,page:1}).then(function (result) {
                    if (result.data.flag){
                        _this.newslist = result.data.data;
                        _this.total =result.data.data[0].total
                    } else{
                        alert(result.data.msg)
                    }
                })
            },

            // 平铺数据转树形数据2
            // table2json:function(obj){
            //     let defaultjson = [];
            //     let json = defaultjson;
            //     for(let i=0;i<obj.length;i++){
            //         let newObj = {id:obj[i].id,label:obj[i].title,children:[]};
            //         json = defaultjson;
            //         for(let x=1;x<obj[i].level;x++){
            //             json = json[json.length - 1].children;
            //         }
            //         json.push(newObj);
            //     }
            //     return defaultjson;
            // },

            // 平铺数据转树形数据2
            table2json:function(table,parentId=0){
                let arr = [];
                for (let i=0;i<table.length;i++){
                    let row = table[i];
                    if(row.parentId === parentId){
                        arr.push({
                            label:row.title,
                            id:row.id,
                            children:this.table2json(table,row.id)
                        })
                    }
                }
                return arr;
            },

            // 获取标题及新闻列表
            getClass:function () {
                const _this = this;
                this.$ajax.get(`/users/getClass`).then(function (result) {
                    if(result.data.flag){
                        // _this.qwerList = JSON.parse(result).data
                        // const json = _this.table2json(JSON.parse(result).data);
                        console.log(result.data.data)
                        _this.classdata = result.data.data;
                    }
                });
                this.$ajax.post(`/api/newslist`,{arrId:0,page:1}).then(function (result) {
                    if(result.data.flag){
                        // alert(result.data.msg)
                        _this.newslist = result.data.data;
                        _this.total =result.data.data[0].total
                    }else {
                        alert(result.data.msg)
                    }
                })
            },

            // 侧栏控制
            change(){
                this.hidden.key = !this.hidden.key;
                // this.hidden.left
                // this.hidden.right
                const _this = this;
                if (this.hidden.key){
                    let hhh = setInterval(aa,10);
                    this.hidden.icon = "el-icon-arrow-left";
                    function aa(){
                        if (_this.hidden.left < 6){
                            _this.hidden.left +=1;
                            _this.hidden.right -=1;
                        } else {
                            clearInterval(hhh);
                        }
                    }
                } else{
                    let hhh = setInterval(aa,10);
                    this.hidden.icon = "el-icon-arrow-right";

                    function aa(){
                        if (_this.hidden.left > 0){
                            _this.hidden.left -=1;
                            _this.hidden.right +=1;
                        } else {
                            clearInterval(hhh);
                        }
                    }
                }
            },

            // 取消小广告
            escad(){
                this.adview="display:none";

            }
        },

        created() {
            this.getClass();
        },
        handleChange(val) {
            console.log(val);
        }

    }
</script>

<style scoped>
    .wrapper{
        width: 1200px;
        margin: 0 auto;
        background-image: url("https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1565152389390&di=4e886467b46a320753db583d63894951&imgtype=0&src=http%3A%2F%2Fwww.51pptmoban.com%2Fd%2Ffile%2F2012%2F11%2F05%2F06a3781f36c3bcdd50f951388beb3403.jpg");
        background-size: 100%;
        background-repeat: no-repeat;
        border-radius: 10px;
    }
    .wrapper .title{
        height: 100px;
        line-height: 100px;
        width: 100%;
        color: white;
        border-radius: 10px;
        font-size: 50px;
        text-align: center;
        font-weight: bold;
        background-image: url("http://img2.imgtn.bdimg.com/it/u=1134739909,1340241433&fm=26&gp=0.jpg");

    }
    .grid-content {
        border-radius: 4px;
        /*height: 36px;*/
        background-color: transparent;
        position: relative;
        overflow-y: auto;
    }
    .grid-content .list{
        /*height: 60px;*/
        /*line-height: 60px;*/
        background-color: white;
        font-size: 30px;
    }
    /*.grid-content .list .list001{*/
        /*height: 60px;*/
        /*line-height: 60px;*/
    /*}*/
    .grid-content .list .content{
        padding: 30px;
        background-color: black;
        color: white;
        font-size: 20px;
        text-align: left;
        text-indent: 40px;
    }


    .wrapper .box{
        margin: 20px 0 20px;
        position: relative;
        height: 1000px;
    }
    .box .pagination{
        position: absolute;
        bottom: 220px;
        left: 0;
        width: 100%;
        display: flex;
    }
    .box .pagination .page{
        margin: auto;
    }
    .wrapper .box:hover .icon001{
        display: block;
    }
    .wrapper .box .icon001{
        position: absolute;
        left: 0;
        top:400px;
        display: none;
        z-index: 9;

    }
    .box .ad001{
        position: absolute;
        right: -200px;
        bottom: 300px;
    }
    .box .ad001 .ad .text{
        text-align: left;
    }
    .box .ad001 .ad .icon0001{
        width: 40px;
        height: 40px;
        display: inline-block;
    }
    .box .ad001 .ad .icon0001 .icon0002{
        color: red;
    }





</style>