<template>
    <div>
        标题：<input type="text" v-model="title">
        内容：<input type="text" v-model="content">
        <button @click="submit">提交</button>
    </div>

</template>

<script>
    export default {
        name: "surperAdmin",
        data:function () {
            return {
                title:'',
                content:''
            }
        },
        methods:{
            submit:function () {
                console.log(this.title,this.content)
                this.$ajax.post('/api/news',{title2:this.title,content2:this.content}).then(function (result) {
                    if(result.data.flag){
                        alert(result.data.msg)
                    }else {
                        alert(result.data.msg)

                    }
                })
            }
        }
    }
</script>

